.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 50px 120px;
    color: #fff;
}
.nav-logo {
    font-family: outfit;
    font-size: 42px;
    font-weight: 500;
}
.nav-menu {
    display: flex;
    align-items: center;
    list-style: none;
    font-size: 18px;
    gap: 70px;
}
.nav-contact {
    border-radius: 50px;
    padding: 10px 38px;
    background: #fff;
    color: #262626;
};
